import React from 'react';
import { useLiveQuery } from "dexie-react-hooks";
import Dexie from 'dexie';
import { UtilManager } from '../../Utils';
//import { db } from "./db";

class ResourceViewer extends React.Component {
    state = {
        text: ''
    }

    async getText() {
        //conectar a la db indexeDB y retonar el texto
        const { resourceTag } = this.props;        
        let result = await UtilManager.GetResource(resourceTag);
        this.setState({ text: result });
    }

    async componentDidMount() {
        this.setState({ text: this.props.resourceTag });
        await this.getText();
    }

    render() {
        return (
            <>
                {this.state.text}
            </>
        );
    }
}

export default ResourceViewer;