import React, { Component } from 'react';
import { ReactComponent as EmptyImage } from '../images/empty_report.svg';
import ResourceViewer from './Visualization/ResourceViewer';

export class EmptyState extends Component {
    constructor(props) {
		super(props)
        this.state = {
            is_list_view: props.isListView
        }

    }

    render() {
        return (
                <div className="table-empty-placeholder">
                    {this.state.is_list_view ? (<></>) : (
                        <>
                            <EmptyImage />
                            <span><ResourceViewer resourceTag="BLFlex.Span.SelectColumn" /></span>
                        </>
                    )}
                </div>
            
        )
    }
}

export default EmptyState
